.portfolio-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.portfolio-title {
  text-align: center;
  margin-bottom: 2rem;
  font-size: 2.5rem;
  color: #333;
}

.portfolio-filter {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.filter-button {
  background-color: #f0f0f0;
  border: none;
  padding: 0.6rem 1.2rem;
  border-radius: 20px;
  cursor: pointer;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.filter-button:hover {
  background-color: #e0e0e0;
}

.filter-button.active {
  background-color: #4a89dc;
  color: white;
}

.projects-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}

.project-card {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

@media (min-width: 768px) {
  .project-card {
    flex-direction: row;
    height: 350px;
  }
}

.project-card:hover {
  transform: translateY(-5px);
}

.project-image {
  flex: 0 0 40%;
  overflow: hidden;
}

.project-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s ease;
}

.project-card:hover .project-image img {
  transform: scale(1.05);
}

.project-info {
  flex: 1;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
}

.project-info h2 {
  margin-top: 0;
  margin-bottom: 0.5rem;
  color: #333;
}

.project-date {
  color: #666;
  font-size: 0.9rem;
  margin-bottom: 1rem;
}

.project-description {
  margin-bottom: 1.5rem;
  line-height: 1.6;
  flex-grow: 1;
}

.project-tech h3 {
  margin-bottom: 0.5rem;
  font-size: 1.1rem;
  color: #444;
}

.tech-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.tech-tag {
  background-color: #f0f0f0;
  padding: 0.3rem 0.8rem;
  border-radius: 15px;
  font-size: 0.8rem;
  color: #555;
}

.project-links {
  display: flex;
  gap: 1rem;
}

.project-link {
  display: inline-block;
  padding: 0.6rem 1.2rem;
  background-color: #4a89dc;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  font-size: 0.9rem;
  transition: background-color 0.3s ease;
}

.project-link:hover {
  background-color: #3a79cc;
}

.demo-link {
  background-color: #5cb85c;
}

.demo-link:hover {
  background-color: #4ca84c;
}
