.blog-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.blog-title {
  text-align: center;
  margin-bottom: 2rem;
  font-size: 2.5rem;
  color: #333;
}

.loading {
  text-align: center;
  padding: 2rem;
  font-size: 1.2rem;
  color: #666;
}

/* 포스트 목록 스타일 */
.posts-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
}

.post-card {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 1.5rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.post-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.post-title {
  margin-top: 0;
  margin-bottom: 1rem;
  color: #333;
  font-size: 1.5rem;
}

.post-summary {
  margin-bottom: 1.5rem;
  line-height: 1.6;
  color: #555;
  flex-grow: 1;
}

.post-footer {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.post-date {
  color: #666;
  font-size: 0.9rem;
}

.post-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.post-tag {
  background-color: #f0f0f0;
  padding: 0.3rem 0.8rem;
  border-radius: 15px;
  font-size: 0.8rem;
  color: #555;
}

/* 포스트 상세 페이지 스타일 */
.post-detail {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 2rem;
}

.back-button {
  background-color: transparent;
  border: none;
  color: #4a89dc;
  cursor: pointer;
  font-size: 1rem;
  padding: 0;
  margin-bottom: 1.5rem;
  display: inline-block;
  transition: color 0.3s ease;
}

.back-button:hover {
  color: #3a79cc;
  text-decoration: underline;
}

.post-header {
  margin-bottom: 2rem;
  border-bottom: 1px solid #eee;
  padding-bottom: 1.5rem;
}

.post-header h2 {
  margin-top: 0;
  margin-bottom: 1rem;
  font-size: 2rem;
  color: #333;
}

.post-meta {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}

.post-content {
  line-height: 1.8;
  color: #333;
}

/* 마크다운 콘텐츠 스타일 */
.markdown-content {
  white-space: pre-wrap;
  font-family: inherit;
  line-height: 1.8;
  overflow-x: auto;
  background-color: transparent;
  padding: 0;
  margin: 0;
  color: #333;
}

/* 반응형 스타일 */
@media (max-width: 768px) {
  .posts-list {
    grid-template-columns: 1fr;
  }

  .post-meta {
    flex-direction: column;
    align-items: flex-start;
  }
}
